/* BASE */
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust-: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.margin-5 {
  margin: 5rem;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-calcular {
  background-color: rgb(22, 0, 130);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline;
  border: none;
}
.boton-calcular:hover {
  background-color: #120069;
  cursor: pointer;
}

/* UI */
.header {
  background-color: #00719a;
  color: #FFFFFF;
}
.header p {
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/Banner_header.png);
}

.webp .inicio {
  background-image: url(../img/Banner_header.webp);
}

.inicio {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Contenedor-menu {
  display: flex;
  padding: 1rem;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.4);
  flex-direction: column;
}
@media (min-width: 768px) {
  .Contenedor-menu {
    flex-direction: row;
  }
}
.Contenedor-menu .Logo {
  margin-left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .Contenedor-menu .Logo {
    margin-left: 3rem;
  }
}
.Contenedor-menu .Logo a {
  text-decoration: none;
  color: #FFFFFF;
}
.Contenedor-menu .Logo .logo-image {
  max-width: 4rem;
  margin-right: 0.5rem;
}
.Contenedor-menu .enlaces_header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Contenedor-menu .enlaces_header ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}
@media (min-width: 768px) {
  .Contenedor-menu .enlaces_header ul {
    flex-direction: row;
  }
}
.Contenedor-menu .enlaces_header ul li {
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .Contenedor-menu .enlaces_header ul li {
    margin-right: 3rem;
  }
}
.Contenedor-menu .enlaces_header ul li a {
  text-decoration: none;
  color: #FFFFFF;
}

.contenedor-header {
  background-color: rgba(0, 0, 0, 0.4);
}
.contenedor-header .contenido-header {
  padding: 3rem;
}
.contenedor-header .contenido-header h1 {
  font-weight: bold;
}
.contenedor-header .contenido-header p {
  margin: 3rem 6rem;
  text-align: center;
}
.contenedor-header .contenido-header .botones-header {
  padding: 2rem;
}
.contenedor-header .contenido-header .botones-header a {
  text-decoration: none;
  color: #FFFFFF;
  background-color: rgb(107, 0, 116);
  padding: 1rem;
}
.contenedor-header .contenido-header .botones-header a:hover {
  background-color: rgb(56, 0, 61);
}
.contenedor-header .contenido-header .botones-header a:first-child {
  margin: 0 3rem;
}

main {
  border: 1px solid transparent;
  background-color: #1e293b;
  color: #d4d4d4;
  padding: 3rem 0;
}
main p {
  color: #FFFFFF;
}

.section-principal .parrafos-titulo {
  text-align: left;
  font-size: 2.5rem;
  font-weight: bold;
  text-transform: uppercase;
  margin: 2rem 5rem;
}

.contenedor-articulos {
  padding: 2rem;
}
.contenedor-articulos .articulos {
  background-color: #0f131b;
  margin: 3rem;
  padding: 3rem 4rem;
  border-radius: 1rem;
}
.contenedor-articulos .articulos p {
  color: #FFFFFF;
}

.mas-informacion .boton-amarillo, .mas-informacion .boton-verde {
  display: block;
}
@media (min-width: 768px) {
  .mas-informacion .boton-amarillo, .mas-informacion .boton-verde {
    display: inline;
    margin-right: 3rem;
  }
}

@media (min-width: 768px) {
  .seccion-resultados {
    display: flex;
    justify-content: space-between;
  }
}

.resultados {
  padding: 0 8rem;
  background-color: #0f131b;
  margin: 5rem;
  border-radius: 1rem;
  text-align: center;
  height: fit-content;
  max-width: 50rem;
}
.resultados h3 {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.5rem;
}
.resultados .result p {
  color: #FFFFFF;
}

.form-div h3 {
  margin-top: 0;
}
.form-div .form-info {
  padding: 0 4rem;
  text-align: justify;
}
@media (min-width: 768px) {
  .form-div {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .form-div .form-info {
    padding: 0 7rem 0 3rem;
  }
  .form-div .form-info p {
    font-size: 1.8rem;
  }
}

.formulario {
  margin: 3rem 7rem;
}
.formulario fieldset legend {
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: bold;
}
.formulario fieldset label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario fieldset input {
  display: block;
  padding: 1rem;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
}
.formulario fieldset select {
  padding: 1rem;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
}
.formulario fieldset input, .formulario fieldset select {
  margin-bottom: 1rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 8rem;
  resize: none;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 7rem;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

@media (min-width: 768px) {
  .footer {
    display: flex;
  }
}
.footer h3 {
  margin: 0;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.5rem;
  text-align: left;
  padding: 2rem 2rem 0 2rem;
}

.Contenedor-footer {
  width: 60%;
}
.Contenedor-footer .Fuentes-footer a {
  text-decoration: none;
  color: #333333;
}
.Contenedor-footer .Fuentes-footer ul {
  list-style: none;
  text-decoration: none;
  margin: 0;
}
@media (min-width: 768px) {
  .Contenedor-footer .Fuentes-footer ul {
    display: flex;
    padding-bottom: 2rem;
  }
}
@media (min-width: 768px) {
  .Contenedor-footer .Fuentes-footer ul li {
    margin-right: 1rem;
  }
  .Contenedor-footer .Fuentes-footer ul li:last-child {
    margin: 0;
  }
}

.Contenedor-footer2 {
  display: block;
}
@media (min-width: 768px) {
  .Contenedor-footer2 {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.Contenedor-footer2 .footer-final p {
  margin: 0;
  font-size: 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .Contenedor-footer2 .footer-final p {
    font-size: 2rem;
  }
}

.webp .seccion-dieta {
  background-image: url(../img/fondo-dieta.webp);
}

.no-webp .seccion-dieta {
  background-image: url(../img/fondo-dieta.jpg);
}

.seccion-dieta {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.seccion-dieta h2 {
  margin: 0;
  padding: 2.5rem;
  font-weight: bold;
}
.seccion-dieta h2, .seccion-dieta .dieta {
  background-color: rgba(0, 0, 0, 0.4);
}
.seccion-dieta .dieta {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .seccion-dieta .dieta {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.seccion-dieta .resultados {
  padding: 2rem 0 4rem 0;
}
.seccion-dieta .resultados:first-child {
  margin-top: 0;
}
@media (min-width: 768px) {
  .seccion-dieta .resultados:first-child {
    margin-top: 5rem;
  }
}

@media (min-width: 768px) {
  .re-error {
    grid-column: 2/3;
  }
  .re-error .result {
    margin: 0 2rem;
  }
}
/* Seccion Otrainfo*/
@media (min-width: 768px) {
  .seccion-alimentos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.titulo-info {
  margin: 0 auto;
  max-width: 100rem;
  padding: 2rem;
  border-bottom: 1px solid #FFFFFF;
}

/*MacroNutrientes Seccion*/
.no-webp .contenedor-macronutri {
  background-image: url(../img/macronutrientes-fond.jpg);
}

.webp .contenedor-macronutri {
  background-image: url(../img/macronutrientes-fond.webp);
}

.contenedor-macronutri, .concepto, .rutinas-back {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.macronutri-contenido {
  background: rgb(15, 19, 27);
  background: linear-gradient(90deg, rgb(15, 19, 27) 0%, rgb(28, 34, 49) 98%, rgba(15, 19, 27, 0) 100%);
  max-width: 85rem;
}
.macronutri-contenido .macronutri-concepto {
  padding: 4rem;
}

/*Rutinas seccion*/
.no-webp .rutinas-back {
  background-image: url(../img/rutinas-fond.jpg);
}

.webp .rutinas-back {
  background-image: url(../img/rutinas-fond.webp);
}

.rutinas-back {
  margin: 5rem 0;
}
.rutinas-back .rutinas {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 3rem 6rem;
}
.rutinas-back .rutinas h2, .rutinas-back .rutinas p {
  max-width: 120rem;
  margin: 0 auto;
}
.rutinas-back .rutinas h2 {
  font-weight: bold;
}
.rutinas-back .rutinas .rutinas-view {
  max-width: 20rem;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 3rem 0.5rem;
  margin: 2rem;
  border-radius: 1rem;
}
.rutinas-back .rutinas .rutinas-view p {
  font-size: 1.4rem;
  text-align: center;
}
.rutinas-back .rutinas .rutinas-flex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .rutinas-back .rutinas .rutinas-flex {
    flex-direction: row;
  }
}

.research-item {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.5rem;
  padding: 3rem 5rem;
  margin: 3rem 0;
}

.no-webp .c-agua {
  background-image: url(../img/agua-fond.jpg);
}

.webp .c-agua {
  background-image: url(../img/agua-fond.webp);
}

.no-webp .c-fibra {
  background-image: url(../img/fibra-fond.jpg);
}

.webp .c-fibra {
  background-image: url(../img/fibra-fond.webp);
}

.no-webp .c-antioxidantes {
  background-image: url(../img/antioxidantes-fond.jpg);
}

.webp .c-antioxidantes {
  background-image: url(../img/antioxidantes-fond.webp);
}

.no-webp .c-alimentos {
  background-image: url(../img/alimentos-fond.jpg);
}

.webp .c-alimentos {
  background-image: url(../img/alimentos-fond.webp);
}

/*Seccion Conceptos*/
@media (min-width: 768px) {
  .seccion-conceptos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.concepto {
  padding: 4rem 5rem;
}

.contenedor-conceptos {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
  padding: 3rem;
}

