  padding: 5rem;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
}

/* Panel de admin */
table.tabla-admin {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.tabla-admin thead {
  background-color: #71B100;
}
table.tabla-admin th, table.tabla-admin td {
  text-align: center;
  padding: 2rem;
}

section.contenedor {
  max-width: 140rem;
}